/* eslint-disable react/no-danger */
import dynamic from 'next/dynamic';
import { memo } from 'react';
import { I_Post } from '@containers/Home/types';
import { DialogHotspotArticleWrapper } from './styled';

const DialogContainer = dynamic(() => import('@components/DialogContainer'));

interface Props {
  width?: number | string;
  post?: I_Post;
  visible: boolean;
  onCancel: () => void;
}

export const DialogArticle = ({ visible, onCancel, post, width }: Props) => {
  return (
    <DialogContainer
      visible={visible}
      onCancel={onCancel}
      maskClosable
      centered
      width={width || '720px'}
      bodyStyle={{ maxWidth: width || '720px', width: '100%' }}
    >
      <DialogHotspotArticleWrapper>
        <div className="header">
          {post?.thumbnail?.src && (
            <img
              className="thumb"
              src={post?.thumbnail?.src}
              alt={post?.thumbnail?.name}
            />
          )}
          <div className="title">{post?.title || 'N/A'}</div>
        </div>
        <div className="body hide-scrollbar">
          <div
            className="content ctn__html"
            dangerouslySetInnerHTML={{ __html: post?.content }}
          />
        </div>
      </DialogHotspotArticleWrapper>
    </DialogContainer>
  );
};

export default memo(DialogArticle);
